.vis-root {
  height: 100vh;
  text-align: center;
}

.question-root {
  background: #c31432; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #240b36,
    #c31432
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    right,
    #240b36,
    #c31432
  );
  background: linear-gradient(
    to left,
    #240b36,
    #c31432
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  text-align: center;
  color: white;
  padding-top: 50px;
}

.question-text {
  font-size: 1.5em;
  margin: 50px;
}

.landing-root {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #2c5364,
    #203a43,
    #0f2027
  );
  background: linear-gradient(
    to right,
    #2c5364,
    #203a43,
    #0f2027
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  text-align: center;
  color: white;
  padding-top: 50px;
}

.landing-text {
  font-size: 1.5rem;
  line-height: 3rem;
  margin: 50px;
}

@media only screen and (max-width: 992px) {
  .landing-text {
    font-size: 1rem;
    line-height: 2rem;
    margin: 50px;
  }
}

path:hover {
  fill: #fcbc34;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

